import React, { useState, useEffect, useRef, useCallback } from "react";
//import axios from "axios";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/styles/ag-grid.css";
import "ag-grid-community/styles/ag-theme-alpine.css";
import "./Mout_state.css";

import mockVehicleList from "./mock/mockVehicleList";
import mockOutboundData from './mock/mock_mout_state';  //    

//const API_BASE_URL = "http://34.64.211.3:5004";

const Mout_state = () => {
  const [searchText, setSearchText] = useState("");
  const [tableData, setTableData] = useState([]);
  const [selectedRowData, setSelectedRowData] = useState(null);
  const [showBackupData, setShowBackupData] = useState(false);
  const detailRef = useRef(null);
  const [selectedStatus, setSelectedStatus] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [vehicleList, setVehicleList] = useState([]);
  const [selectedVehicleId, setSelectedVehicleId] = useState(null);
  const [showVideoModal, setShowVideoModal] = useState(false);
  const [videoActionType, setVideoActionType] = useState(""); // "start" or "complete"
  const [videoSrc, setVideoSrc] = useState("");


  const handleOutsideClick = (event) => {
    if (detailRef.current && !detailRef.current.contains(event.target)) {
      setSelectedRowData(null);
    }
  };

  const fetchTableData = useCallback(async () => {
  try {
    //   ( + )
    const filtered = mockOutboundData.filter((item) => {
      const matchSearch =
        item.company_name.includes(searchText) ||
        item.warehouse_location.includes(searchText) ||
        item.outbound_status.includes(searchText);
      const matchStatus = selectedStatus ? item.outbound_status === selectedStatus : true;
      return matchSearch && matchStatus;
    });

    setTableData(filtered);
    setSelectedRowData(null);
  } catch (error) {
    console.error("Error fetching mock table data:", error);
  }
}, [searchText, selectedStatus]);


  useEffect(() => {
  fetchTableData();
  document.addEventListener("click", handleOutsideClick);
  return () => {
    document.removeEventListener("click", handleOutsideClick);
  };
}, [fetchTableData]);


  const handleRowClick = (event) => {
    setSelectedRowData(event.data);
  };

  const handleSave = () => {
  alert("   . (mock)");

  const updated = mockOutboundData.map(item =>
    item.id === selectedRowData.id
      ? { ...item, outbound_status: "", last_outbound_date: new Date().toISOString().slice(0, 10) }
      : item
  );

  setTableData(updated);
  setSelectedRowData(null);
};



  const dateFormatter = (params) => {
    if (!params.value) return "";
    const date = new Date(params.value);
    return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, "0")}-${String(date.getDate()).padStart(2, "0")}`;
  };

  const gridRef = useRef(null);

  const handleRefresh = async () => {
  alert(" (Mock)   .");
  fetchTableData();
};

  const handleStartPreparation = () => {
  if (!selectedRowData) {
    alert("  .");
    return;
  }
  alert("  ' '  (mock )");

  // mock data 
  const updated = mockOutboundData.map(item =>
    item.id === selectedRowData.id
      ? { ...item, outbound_status: " " }
      : item
  );

  setTableData(updated);
  setSelectedRowData(null);
};



  const handleCompletePreparation = () => {
  alert("  '  '  (mock )");

  const updated = mockOutboundData.map(item =>
    item.id === selectedRowData.id
      ? { ...item, outbound_status: "  " }
      : item
  );

  setTableData(updated);
  setSelectedRowData(null);
};


  const fetchVehicleList = async (mainTableId) => {
  setVehicleList(mockVehicleList); //  mock 
};


  useEffect(() => {
    if (showModal && selectedRowData) {
      fetchVehicleList(selectedRowData.id);
      setSelectedVehicleId(null);
    }
  }, [showModal, selectedRowData]);

  const handleAssignVehicle = () => {
  alert("  . (mock)");

  const updated = mockOutboundData.map(item =>
    item.id === selectedRowData.id
      ? { ...item, outbound_status: " ", last_outbound_date: new Date().toISOString().slice(0, 10) }
      : item
  );

  setTableData(updated);
  setSelectedRowData(null);
  setShowModal(false);
};

  
  const handleVideoEnd = async () => {
  setShowVideoModal(false);

  //    API    mock alert 
  alert(" (Mock)       ");
  fetchTableData();
  setSelectedRowData(null);
};

  

  const [activeTab, setActiveTab] = useState('');
  const statusTabs = ['', ' ', '  ', ' ', ''];
  const filteredData = tableData.filter(item => item.outbound_status === activeTab);

  return (
    <div className="container">
      <div className="content">
        <h2 className="sectionTitle"> </h2>

        <div className="searchContainer">
          <input
            type="text"
            placeholder="Search ( ,  ,  )"
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
            className="inputField"
          />

          <button
            onClick={handleRefresh}
            className="buttonPrimary"
            style={{ backgroundColor: "#6f47c5" }}
          >
            
          </button>
        </div>

        {/*    UI */}
        <div style={{ display: 'flex', gap: '10px', marginBottom: '15px' }}>
          {statusTabs.map(status => (
            <button
              key={status}
              onClick={() => setActiveTab(status)}
              style={{
                padding: '10px 20px',
                border: 'none',
                borderBottom: activeTab === status ? '3px solid #6f47c5' : '3px solid transparent',
                background: 'transparent',
                color: activeTab === status ? '#6f47c5' : '#333',
                fontWeight: 'bold',
                cursor: 'pointer'
              }}
            >
              {status}
            </button>
          ))}
        </div>

        <div className="ag-theme-alpine" style={{ height: "600px", width: "100%", minHeight: "400px" }}>
          <AgGridReact
            ref={gridRef}
            rowData={filteredData}
            domLayout="autoHeight"
            onGridReady={(params) => {
              params.api.sizeColumnsToFit(); //    
            }}
            columnDefs={[
              { headerName: "", field: "company_name", sortable: true, filter: true },
              { headerName: "", field: "product_name", sortable: true, filter: true },
              { headerName: "", field: "inbound_quantity", sortable: true, filter: true },
              { headerName: "", field: "warehouse_location", sortable: true, filter: true },
              { headerName: "", field: "warehouse_type", sortable: true, filter: true },
              { headerName: "", field: "outbound_status", sortable: true, filter: true },
              { headerName: "", field: "outbound_date", sortable: true, filter: true, valueFormatter: dateFormatter },
              { headerName: "", field: "last_outbound_date", sortable: true, filter: true, valueFormatter: dateFormatter },
            ]}
            onRowClicked={handleRowClick}
            pagination={true}
            paginationPageSize={10}
            defaultColDef={{ sortable: true, filter: true, resizable: true, suppressMovable: true }}
            gridOptions={{ headerHeight: 50, rowHeight: 40 }}
          />
        </div>

        {selectedRowData && selectedRowData.outbound_status !== "  " && (
          <div className="modalOverlay">
            <div className="modalBox">
              <div className="modalHeader">
                <h3 style={{ margin: 0 }}>  </h3>
                <button className="modalCloseBtn" onClick={() => setSelectedRowData(null)}></button>
              </div>
              <table className="infoTable">
                <tbody>
                  <tr><th></th><td>{selectedRowData.company_name}</td></tr>
                  <tr><th></th><td>{selectedRowData.product_name}</td></tr>
                  <tr><th> </th><td>{selectedRowData.inbound_quantity}</td></tr>
                  <tr><th> </th><td>{selectedRowData.warehouse_location}</td></tr>
                  <tr><th> </th><td>{selectedRowData.warehouse_type}</td></tr>
                  <tr><th> </th><td>{selectedRowData.outbound_status}</td></tr>
                </tbody>
              </table>
              <div style={{
                    marginTop: "20px",
                    display: "flex",
                    justifyContent: "center", //  
                    gap: "10px", //   
                    flexWrap: "wrap",
                  }}
                >
                {selectedRowData.outbound_status === "" && (
                  <button
                    className="prep-button"
                    onClick={handleStartPreparation}
                  >
                      
                  </button>
                )}

                {selectedRowData.outbound_status === " " && (
                  <button
                    className="prep-button"
                    onClick={handleCompletePreparation}
                  >
                      
                  </button>
                )}

                <button
                  onClick={handleSave}
                  className="buttonPrimary"
                  style={{
                    cursor: selectedRowData.outbound_status === "" ? "pointer" : "not-allowed",
                    marginTop: "10px"
                  }}
                  disabled={selectedRowData.outbound_status !== ""}
                >
                   
                </button>
              </div>
            </div>
          </div>
        )}

        {selectedRowData && selectedRowData.outbound_status === "  " && (
          <div className="modalOverlay">
            <div className="modalFlex">
              <div className="modalHeader">
                <span className="modalTitle">    </span>
                <button className="modalCloseBtn" onClick={() => setSelectedRowData(null)}></button>
              </div>

              <div className="modalContentWrapper">
                {/*  :    */}
                <div className="modalLeft">
                  <table className="infoTable">
                    <tbody>
                      <tr><th></th><td>{selectedRowData.company_name}</td></tr>
                      <tr><th></th><td>{selectedRowData.product_name}</td></tr>
                      <tr><th> </th><td>{selectedRowData.inbound_quantity}</td></tr>
                      <tr><th> </th><td>{selectedRowData.warehouse_location}</td></tr>
                      <tr><th> </th><td>{selectedRowData.warehouse_type}</td></tr>
                      <tr><th> </th><td>{selectedRowData.outbound_status}</td></tr>
                    </tbody>
                  </table>
                </div>

                {/*  :  UI */}
                <div className="modalRight">
                  <h3>   </h3>
                  {vehicleList.length === 0 ? (
                    <p>   .</p>
                  ) : (
                    <ul>
                      {vehicleList.map((vehicle) => (
                        <li key={vehicle.id} style={{ marginBottom: "6px" }}>
                          <label>
                            <input
                              type="radio"
                              name="vehicle"
                              value={vehicle.id}
                              checked={selectedVehicleId === vehicle.id}
                              onChange={() => setSelectedVehicleId(vehicle.id)}
                            />
                            [{vehicle.truck_type}] {vehicle.driver_name} ({vehicle.driver_phone}) - {vehicle.current_location}  {vehicle.destination}
                          </label>
                        </li>
                      ))}
                    </ul>
                  )}

                  <div style={{ marginTop: "20px", textAlign: "center" }}>
                    <button
                      className="buttonPrimary"
                      disabled={!selectedVehicleId}
                      onClick={handleAssignVehicle}
                    >
                       
                    </button>
                    
                  </div>
                  
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};



export default Mout_state;
